<template>
  <div class="add">
    <v-dialog
      v-model="dialog"
      persistent
      content-class="no-box-shadow">
      <div class="text-center">
        <v-progress-circular
          size="80"
          width="8"
          indeterminate
          color="primary">
        </v-progress-circular>
      </div>
    </v-dialog>
    <h1>Add JSON</h1>
      <div
        class="error-message"
        v-if="unavailable">
        Sorry, server unavailable. <a href="/Add">Refresh.</a>
        <v-icon class="white--text">error_outline</v-icon>
      </div>
      <v-textarea
        v-model="jsonstring"
        :error-messages="jsonstring === '' ? [] : jsonValidation === '' ? ['Invalid JSON.'] : []" 
        v-on:input="error = !error"
        label="Type here...">
      </v-textarea>
      <pre
        v-text="jsonValidation"
        class="secondary white--text">
      </pre>
      <v-btn
        :disabled="unavailable === true ? true : jsonValidation === '' ? true : jsonValidation === 'Invalid JSON.' ? true : false" 
        @click="addJson"
        color="secondary">submit json
      </v-btn>
      <div v-if="uploadsuccessful===true">upload successful</div>
      <div v-else-if="uploadsuccessful===false">upload failed</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
    jsonstring: '',
    error: false,
    host: 'http://localhost:3333',
    unavailable: false,
    dialog: true,
    uploadsuccessful: '',
    //host: 'http://oryx-json-server.rokskrbec.si'
    }  
  },
  mounted() {
    fetch(`${this.host}/api/json`)
    .then((res) => res.json())
    .then(() => {
      //console.log(data)
      this.dialog = false
    })
    .catch((err) => {
      this.unavailable = true
      this.dialog = false
      console.log('err: ' + err)
    })
  },
  methods: {
    addJson() {
      //let proxy = 'https://cors-anywhere.herokuapp.com/'
      let proxy = ''
      fetch(`${proxy}${this.host}/api/json`,  //!! TODO Fix 
      {      
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
        },
        method: 'PUT',
        body: this.jsonstring
      }).then(res => {
        res.text()
      }).then(data => {
        console.log(data)
      }).catch(err => {
        alert(err)
        console.log('err: ' + err)
        this.unavailable = true
        this.uploadsuccessful = false
      })
        //console.log('Upload successful!')
        //this.uploadsuccessful = true
        //this.jsonstring = ''
    }
  },
  computed: {
    jsonValidation() {
      let jsonValue
      try {
        jsonValue = JSON.parse(this.jsonstring)
      } catch (e) {
        return ''
      }
      return JSON.stringify(jsonValue, null, 4)
    },
  },
}
</script>

